import React from 'react';
import Login from './Login';

const Buscador = () => {
    
    return( <div className="contenedorBuscador">
    <input className="buscador1" type="text" placeholder="¿Qué estás buscando?" />
            </div>
    );
};


const Section = () => {
    return (
        <section>           
            <Login/>
            <Buscador/>
       </section>
    );
};

export default Section;